@import "./tokens.css";

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: var(--font-family-base);
  background: var(--color-bg);
  color: var(--color-text);
  line-height: var(--line-height-normal);
}

#app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1;
  display: flex;
  flex-direction: column;
}

button, input {
  font-family: inherit;
}

button {
  cursor: pointer;
  border: none;
  border-radius: var(--radius-sm);
  padding: 0.5rem 1rem;
  font-size: var(--font-size-sm);
  background: var(--color-accent);
  color: #fff;
}

button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.app-header {
  border-bottom: 1px solid var(--color-border);
  background: var(--color-surface);
}

.app-nav {
  max-width: 1100px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  gap: var(--space-lg);
  padding: var(--space-md) var(--space-xl);
}

.brand {
  font-size: var(--font-size-lg);
  font-weight: 600;
}

.search-area {
  flex: 1;
  display: flex;
  justify-content: center;
  position: relative;
}

.search-area input[type='search'] {
  width: min(520px, 100%);
  padding: var(--space-sm) var(--space-md);
  border-radius: var(--radius-md);
  border: 1px solid var(--color-border);
  font-size: var(--font-size-md);
  background: var(--color-surface);
  color: var(--color-text);
}

.search-area input[type='search']:focus {
  outline: 2px solid var(--color-accent);
  outline-offset: 2px;
}

.nav-actions {
  display: flex;
  gap: var(--space-sm);
}

main {
  padding: var(--space-2xl) var(--space-xl);
}

.home-ghost {
  max-width: 760px;
  margin: 0 auto;
  padding: var(--space-xl);
  border-radius: var(--radius-md);
  border: 1px dashed var(--color-border);
  text-align: center;
  color: var(--color-text-muted);
}

.app-main {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: var(--space-2xl) var(--space-xl);
}

.screen {
  display: none;
}

.screen:not([hidden]) {
  display: block;
}

.screen--home .home-ghost {
  background: var(--color-surface);
  box-shadow: var(--shadow-soft);
}

.search-form {
  width: 100%;
}

.search-dialog-host {
  position: absolute;
  top: calc(100% + var(--space-sm));
  left: 50%;
  transform: translateX(-50%);
  width: min(640px, 100%);
  z-index: 10;
}

.search-dialog {
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
  background: var(--color-surface);
  border-radius: var(--radius-md);
  box-shadow: var(--shadow-popover);
  border: 1px solid var(--color-border);
  padding: var(--space-lg);
  max-height: 520px;
  overflow-y: auto;
}

.search-dialog__summary {
  font-size: var(--font-size-sm);
  color: var(--color-text-muted);
}

.search-dialog__group h4 {
  margin: 0 0 var(--space-xs);
  font-size: var(--font-size-sm);
  text-transform: uppercase;
  letter-spacing: 0.06em;
  color: var(--color-text-muted);
}

.search-dialog__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: var(--space-sm);
}

.search-dialog__item {
  display: flex;
  flex-direction: column;
  gap: var(--space-2xs);
  padding-bottom: var(--space-sm);
  border-bottom: 1px solid var(--color-border);
}

.search-dialog__item:last-child {
  border-bottom: none;
  padding-bottom: 0;
}

.search-dialog__item-title {
  font-weight: 600;
}

.search-dialog__item-meta {
  font-size: var(--font-size-xs);
  color: var(--color-text-muted);
}

.search-dialog__empty,
.search-dialog__loading,
.search-dialog__error {
  text-align: center;
  color: var(--color-text-muted);
  padding: var(--space-lg);
}

.search-dialog__loading {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--space-md);
}

.spinner {
  width: var(--icon-size-lg);
  height: var(--icon-size-lg);
  border-radius: 50%;
  border: 3px solid var(--color-border);
  border-top-color: var(--color-accent);
  animation: spin 0.8s linear infinite;
}

.search-dialog__footer {
  display: flex;
  justify-content: flex-end;
}

.see-all-button {
  font-weight: 600;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.nav-actions button {
  background: var(--color-surface);
  border: 1px solid var(--color-border);
  color: var(--color-text);
  font-weight: 500;
}

.nav-actions button.is-active {
  background: var(--color-accent);
  color: #fff;
  border-color: var(--color-accent);
}

button.secondary {
  background: var(--color-surface);
  color: var(--color-text);
  border: 1px solid var(--color-border);
}

.results-view {
  display: flex;
  flex-direction: column;
  gap: var(--space-xl);
  max-width: 1100px;
  margin: 0 auto;
}

.results-view__header {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  gap: var(--space-lg);
}

.results-view__summary {
  margin-top: var(--space-xs);
  color: var(--color-text-muted);
}

.results-view__actions button {
  background: none;
  border: none;
  color: var(--color-accent);
  padding: 0;
}

.results-view__facets {
  display: grid;
  gap: var(--space-md);
}

.results-view__facets.is-empty {
  color: var(--color-text-muted);
  font-size: var(--font-size-sm);
}

.results-view__facet-block {
  background: var(--color-surface);
  border: 1px solid var(--color-border);
  border-radius: var(--radius-md);
  padding: var(--space-md);
  display: flex;
  flex-direction: column;
  gap: var(--space-sm);
}

.results-view__facet-list {
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-sm);
}

.facet-chip {
  display: inline-flex;
  align-items: center;
  gap: var(--space-2xs);
  background: var(--color-bg);
  border: 1px solid var(--color-border);
  border-radius: 999px;
  padding: var(--space-2xs) var(--space-sm);
  font-size: var(--font-size-sm);
  color: var(--color-text);
}

.facet-chip.is-selected {
  background: var(--color-accent);
  border-color: var(--color-accent);
  color: #fff;
}

.facet-chip__badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: var(--color-surface);
  color: inherit;
  border-radius: var(--radius-sm);
  padding: 0 var(--space-2xs);
  font-size: var(--font-size-xs);
}

.results-view__groups {
  display: flex;
  flex-direction: column;
  gap: var(--space-lg);
}

.results-group {
  background: var(--color-surface);
  border-radius: var(--radius-md);
  border: 1px solid var(--color-border);
  padding: var(--space-lg);
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
}

.results-group__header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  gap: var(--space-sm);
  border-bottom: 1px solid var(--color-border);
  padding-bottom: var(--space-sm);
}

.results-group__header h2 {
  margin: 0;
}

.results-group__count {
  font-size: var(--font-size-sm);
  color: var(--color-text-muted);
}

.results-group__list {
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
}

.result-card {
  border: 1px solid var(--color-border);
  border-radius: var(--radius-md);
  padding: var(--space-md);
  display: flex;
  flex-direction: column;
  gap: var(--space-sm);
  background: #fff;
}

.result-card__header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  gap: var(--space-sm);
}

.result-card__badge {
  background: var(--color-accent-soft);
  color: var(--color-accent);
  border-radius: 999px;
  padding: 0 var(--space-sm);
  font-size: var(--font-size-xs);
  font-weight: 600;
}

.result-card__summary {
  margin: 0;
  color: var(--color-text-muted);
}

.result-card__meta {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-md);
  margin: 0;
  padding: 0;
}

.result-card__meta span {
  display: block;
  font-size: var(--font-size-xs);
  color: var(--color-text-muted);
}

.result-card__meta strong {
  display: block;
  margin-top: var(--space-2xs);
}

.result-card__line-items {
  border-top: 1px solid var(--color-border);
  padding-top: var(--space-sm);
}

.result-card__line-items h4 {
  margin: 0 0 var(--space-xs);
  font-size: var(--font-size-sm);
}

.result-card__line-items ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: var(--space-sm);
}

.line-item__title {
  font-weight: 600;
}

.line-item__details {
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-sm);
  font-size: var(--font-size-xs);
  color: var(--color-text-muted);
}

.line-item__total {
  font-weight: 600;
  color: var(--color-text);
}

.line-item__more {
  font-size: var(--font-size-sm);
  color: var(--color-text-muted);
}

.results-view__empty {
  text-align: center;
  color: var(--color-text-muted);
}

.settings-view {
  max-width: 720px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: var(--space-lg);
}

.settings-form {
  background: var(--color-surface);
  border: 1px solid var(--color-border);
  border-radius: var(--radius-md);
  padding: var(--space-lg);
  display: flex;
  flex-direction: column;
  gap: var(--space-lg);
}

.settings-field {
  display: flex;
  flex-direction: column;
  gap: var(--space-2xs);
  font-size: var(--font-size-sm);
  color: var(--color-text-muted);
}

.settings-field input {
  padding: var(--space-xs) var(--space-sm);
  border-radius: var(--radius-sm);
  border: 1px solid var(--color-border);
  font-size: var(--font-size-sm);
}

.settings-group__grid {
  display: grid;
  gap: var(--space-sm);
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
}

.settings-actions {
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-sm);
}

@media (max-width: 768px) {
  .app-nav {
    padding: var(--space-md);
    flex-direction: column;
    align-items: stretch;
  }

  .search-area {
    width: 100%;
  }

  .nav-actions {
    justify-content: flex-end;
  }

  .app-main {
    padding: var(--space-lg);
  }
}
